/* 文章内容和信息布局 */
#recent-posts > .recent-post-item > .recent-post-info {
    display: flex !important;
    flex-direction: column !important;
    padding: 0 32px !important;
    justify-content: center !important;
    gap: 8px;
}

#content-inner #recent-posts > .recent-post-item > .recent-post-info > .content {
    opacity: 1 !important;
    height: auto !important;
    line-height: 1.5 !important;
    margin-top: 0 !important;
    font-size: 15px !important;
}

/* 文章顶部提示样式 */
.recent-post-info-top-tips {
    display: flex;
    gap: 10px;
    position: absolute;
    z-index: 999;
    background: rgb(255 255 255 / 20%);
    padding: 0 5px;
    margin: 10px !important;
    border-radius: 6px;
    color: var(--anzhiyu-white);
    backdrop-filter: blur(5px);
}

.recent-post-info-top-tips.right {
    right: 0;
}

.recent-post-info-top-tips.left {
    left: 0;
}

/* 深色模式适配 */
[data-theme='dark'] .recent-post-info-top-tips {
    filter: brightness(0.8);
}

/* 文章元数据样式 */
.article-meta-wrap {
    position: inherit !important;
    padding: 0 !important;
}

.article-meta.sticky-warp,
span.newPost {
    color: #ff7242;
}
.article-categories-original,
span.newPost {
    display: flex;
    gap: 5px;
    align-items: center;
}

span.article-meta.tags,
span.post-meta-date {
    height: 26px;
    line-height: 26px;
}

/* 其他样式 */
.recent-post-info .recent-post-info-top {
    padding: 0 !important;
}

.unvisited-post {
    display: none !important;
}

@media screen and (max-width: 768px) {
    #recent-posts > .recent-post-item > .recent-post-info > .article-meta-wrap {
        bottom: 0 !important;
    }
    .recent-post-info-top-tips.right {
        right: inherit;
    }
}

/* ------------------------------ */
/* 顶部和大部分卡片样式 */
.author-content-item .card-content,
.author-content-item {
    border-radius: 12px!important;
    padding: 1rem 1.5rem!important;
}

/* 目录间距 */
#card-toc {
    margin-bottom: 20px;
}

/* 打赏项容器样式 */
.reward-list-item {
  position: relative;
}

.reward-list-item.with-note::after {
  content: attr(data-note);
  position: absolute;
  inset: 0;
  background: var(--anzhiyu-card-bg);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: all 0.3s ease;
  z-index: 1;
}

.reward-list-item.with-note:hover::after {
  opacity: 1;
}

.reward-list-item.with-note .reward-list-item-name::after {
  content: '🗨️';
  float: right;
  border-radius: 8px;
  padding: 5px;
  margin: -5px;
  border: var(--style-border-always);
}

/* 小动物夜间显示优化 */
[data-theme='dark'] #page-header.nav-fixed #nav {
    background: var(--anzhiyu-black)!important;
}

[data-theme='dark'] #page-header #nav {
    background: 0 !important;
}

/* 定义自定义鼠标指针样式 */
#cursor {
    position: fixed;
    width: 16px;
    height: 16px;
    background: var(--anzhiyu-fontcolor);
    border-radius: 25px;
    opacity: .3;
    z-index: 10086;
    pointer-events: none;
    transition: .2s ease-in-out;
    transition-property: background, opacity, transform;
}

/* 当鼠标指针不活动时的样式 */
#cursor.hidden {
    opacity: 0;
}

/* 当鼠标指针活动时的样式 */
#cursor.active {
    opacity: .5;
    transform: scale(.5);
}

@media (max-width: 768px) {
    #cursor {
        display: none;
    }
}

::selection {
    color: inherit!important;
}

#aside-content .card-info:hover .author-info-avatar {
    opacity: 1;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=1)";
    filter: alpha(opacity = 1);
    -webkit-transform: scale(1);
    -moz-transform: scale(1);
    -o-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
}
.author-info__description {
    display: none;
}
.card-widget.card-info {
    background: url(https://lib.bsgun.cn/Hexo-static/img/avatar.256.avif) center/cover no-repeat!important;
}

#aside-content >.card-widget.card-info::before {
    background-image: linear-gradient(-25deg, hsl(204deg 100% 50% / 45%), hsl(36.71deg 100% 50% / 45%), hsl(204deg 100% 50% / 45%), hsl(36.71deg 100% 50% / 45%));
    backdrop-filter: blur(5px);
}

#aside-content .card-info:hover .author-status {
    opacity: 1!important;
    transform: scale(1.8) rotate(30deg);
}

#article-container .anzhiyu-flink-list .flink-list-item:hover {
    background: var(--anzhiyu-card-bg)!important;
    border: var(--style-border)!important;
    box-shadow: var(--anzhiyu-shadow-border)!important;
}

#article-container .anzhiyu-flink-list .flink-list-item:hover a .flink-item-desc,
#article-container .anzhiyu-flink-list .flink-list-item:hover a .flink-item-name {
    color: var(--anzhiyu-fontcolor)!important;
}

/* 隐藏点赞 */
/* 引用链接隐藏外部提示 */
.tk-action .tk-action-link:first-child,
.tag-link-tips {
    display: none!important;
}

/* 手机端搜索栏样式 */
@media screen and (max-width: 768px), (max-height: 580px) {
    .search-dialog {
        top: 2rem!important;
        left: 5%!important;
        width: 90%!important;
        height: auto!important;
        border-radius: 10px!important;
    }
}
#random-post {
    padding: 15px!important;
    min-height: 64px!important;
}

/* 优化关于界面人生图片 */
img.author-content-img.no-lightbox.entered.loaded {
    filter: blur(0) brightness(.8);
}

/* 滚动条优化 */
#catalog-list,
anzhiyu.barrageContent {
    scrollbar-width: none;
}

/* 优化文章顶部分类和标签显示异常 */
#post-firstinfo .meta-firstline span.post-meta-categories a.post-meta-categories {
    background: rgb(107 107 107 / 50%)!important;
}

#post #post-info #post-firstinfo .meta-firstline .article-meta__tags {
    color: #888!important;
}
/* 链接卡片*/
#article-container .tag-Link .tag-link-bottom {
    margin-top: 0!important;
}

/* 代码块的边框显示优化 */
#article-container figure.highlight {
    border: var(--style-border);
}

/* 手机端nav栏 */
#sidebar #sidebar-menus {
    padding-top: 10px!important;
}

@media screen and (max-width: 768px) {
    /* 在移动端隐藏内容 */
    .card-announcement,
    .card-countdown,
    .card-latest-comments,
    .author-content-item .card-content .banner-button-group {
        display: none !important;
    }
    .post .layout#content-inner {
        background: transparent;
    }
}

/* Twikoo*/
/* 评论框背景图优化 */
.twikoo .el-input__inner,
.twikoo .el-textarea__inner {
    transition: background-position-y 0.3s ease-in-out 0s !important;
}

.twikoo .el-input__inner:focus,
.twikoo .el-textarea__inner:focus {
    background-position-y: 350px;
}

.tk-input .el-textarea__inner {
    -moz-background-size: contain;
    -webkit-background-size: contain;
    -moz-background-size: contain;
    background-size: contain
}

/* 评论回复 */
.tk-action-link {
    border-radius: 10px;
    padding: 5px 10px 5px 10px;
    border: var(--style-border);
}

.tk-action-link:hover {
    background: var(--anzhiyu-theme);
    color: #fff;
}

/* 评论后台优化 */
#body-wrap.page .el-input__inner {
    background: #ffffff0f;
}

[data-theme=dark] details > summary {
    background: none!important;
}

/* 一些页面优化 */
body[data-type="fcircle"] #page,
body[data-type="link"] #page,
body[data-type="essay"] #page {
    transition: padding 0s;
}

#flink-banners {
    animation: none!important;
}

/* 朋友圈骨架屏 全局加载避免位移 */

.articles-container {
    min-height: 624px;
}

.skeleton-content, 
.real-content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    padding: 10px;
    flex-direction: column;
    justify-content: space-between;
    transition: opacity 0.5s ease-in-out;
}

.skeleton {
    opacity: 1;
    position: relative;
    overflow: hidden;
    transform: translateY(0px);
}

.card-title.skeleton {
    min-height: 51px;
}

.card-author.skeleton {
    min-width: 60px;
}

.card-date.skeleton {
    min-height: 26px;
    min-width: 90px;
}

.hidden {
    opacity: 0;
}

@keyframes fadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
}

@keyframes skeleton-shine {
    0% {
        transform: translateX(-100%);
    }
    100% {
        transform: translateX(400%);
    }
}

.skeleton::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 30%;
    height: 100%;
    background: linear-gradient(90deg,transparent,var(--shadow-color),transparent);
    animation: skeleton-shine 1.5s linear infinite;
    will-change: transform;
}


